@import 'https://fonts.googleapis.com/css?family=Raleway';
.navBar {
  margin:0;
  display: flex;
  max-width: 100vw;
  justify-content: right;
  padding-top: 1%;

}



.navBar ul {

	list-style: none;
    height: fit-content;
	padding-left: 3rem;
    padding-right: 3rem;
	display: flex;
}

.navBar li {
    display: flex;
	background-color: #87b4ab;
    align-items: center;
    justify-content: center;
    height: fit-content;
    padding: .5rem .5rem;
    margin-right: 3rem;
    border-radius: .3rem;
    margin-top: 0;
}

.navBar a {
	color: #FFF;
    text-decoration: none;
    font: 20px Raleway;
    margin: 0px 0px;
    padding-top: .05rem;
    padding-bottom: .05rem;
    position: relative;
    z-index: 0;
    cursor: pointer;
}

.navBar a:before, .navBar a:after
{
    position: absolute;
    left: 0px;
    width: 100%;
    height: 2px;
    background: #FFF;
    content: "";
    opacity: 0;
    transition: all 0.3s;
}

.navBar a:before
{
    top: 0px;
    transform: translateY(10px);
}

.navBar a:after
{
    bottom: 0px;
    transform: translateY(-10px);
}

.navBar a:hover:before, .navBar a:hover:after
{
    opacity: 1;
    transform: translateY(0px);
}
